import { Component, OnInit } from '@angular/core';
import { MainServicesService } from './../../../services/main-services.service';
import { AlertController, ModalController } from "@ionic/angular";
import { Router } from '@angular/router';
import { AddDepartmentModalComponent } from '../add-department-modal/add-department-modal.component';
import { EditDeptModalComponent } from '../edit-dept-modal/edit-dept-modal.component';
import $ from 'jquery';
import { ToastrService } from 'ngx-toastr';

import { MenuController } from '@ionic/angular';
import { LoaderService } from 'src/app/services/loader.service';

@Component({
  selector: 'app-departments-list',
  templateUrl: './departments-list.page.html',
  styleUrls: ['./departments-list.page.scss'],
})
export class DepartmentsListPage implements OnInit {
  sortedData = [];
  deptList = [];
  sucmsg
  errmsg;
  pageCounts=Array();  // Will Push total no. of pages in this array
  pageCount:number;    // Total no. of Pages for Department List (data from API)
  page:number;         // Current Page (data from API)
  leftSize: number;
  rightSize: number;
  showMenu: boolean = false;
 
  // showMessage:boolean = false;

  constructor(private menu: MenuController, private main_services: MainServicesService, private router: Router, 
               private modalController: ModalController, private alertCtrl: AlertController, private toastr: ToastrService, private loader:LoaderService) { }

  ngOnInit() { }




  manuToggle() {
    this.showMenu = !this.showMenu;

    if (this.showMenu == false) {
      if ($(window).width() < 992) {
        this.rightSize = 12;
      }
      else if ($(window).width() < 1200) {
        this.leftSize = 3;
        this.rightSize = 9;
      }
      else {
        this.leftSize = 2;
        this.rightSize = 10;
      }
      $('#firstdiv').removeClass("small-aside");
      $('#seconddiv').removeClass("big-right-side");
    }
    else {
      if ($(window).width() < 992) {
        this.rightSize = 12;
      }
      else if ($(window).width() < 1200) {
        this.leftSize = 1;
        this.rightSize = 11;
      }
      else {
        this.leftSize = 1;
        this.rightSize = 11;
      }
      $('#firstdiv').addClass("small-aside");
      $('#seconddiv').addClass("big-right-side");
    }
  }

  hide_mobile_menu() {
    $('#close-show-991').click(function () {

      $('#firstdiv').removeClass("small-aside");
    })
  }

  showMenuMob = true;
  mobileManuToggle() {

    $('#firstdiv').addClass("small-aside");
    // this.showMenuMob = !this.showMenuMob;
  }
  ionViewWillEnter() {
    if ($(window).width() < 992) {
      this.rightSize = 12;
    }
    else if ($(window).width() < 1367) {
      this.leftSize = 3;
      this.rightSize = 9;
    }
    else {
      this.leftSize = 2;
      this.rightSize = 10;
    }
    this.pageCounts= [];
    this.deptList= [];
    this.sortedData=[];
    this.main_services.getDepartmentsList().subscribe(
      (result) => {
        this.pageCount  = result['pageCount'];
        this.page       = result['page'];
        for(let i=1; i<=result['pageCount']; i++)
        {
          this.pageCounts.push(i);
        }
        result['departments'].forEach((value) => {
          this.deptList.push({
            _id               :  value._id,
            branch       :       value.branch.name,
            name  :              value.name,
            description       : value.description
          });
      });
      this.sortedData = this.deptList.slice();
    })
  }

  // Function to switch b/w pages
  paginationData(pageNo){
    this.deptList = [];
    this.main_services.departmentPagination(pageNo).subscribe(
      (result) => {
        this.page = result['page'];
        result['departments'].forEach((value) => {
          this.deptList.push({
            _id               : value._id,
            branch             : value.branch.name,
            name  : value.name,
            description       : value.description
          });
      });
    })
  }

  async openModal() {
    const modal = await this.modalController.create({
      component: AddDepartmentModalComponent,
      cssClass: 'add-department-modal'
    });
    await modal.present();

    const { data: result, role } = await modal.onWillDismiss();

    this.sucmsg = result['message'];
    if (result['success'] == true) {
      this.toastr.success(this.sucmsg);
      this.ionViewWillEnter();
    }
  }


  async openEditModal(dept) {
    const modal = await this.modalController.create({
      component: EditDeptModalComponent,
      componentProps: {
        _id: dept._id,
        // name: list.name,
        // description: list.description
      },
      cssClass: 'edit-dept-modal',
    });
    await modal.present();

    const { data: result, role } = await modal.onWillDismiss();
    this.sucmsg = result['message']; 
    if (result['success'] == true) {
      this.toastr.success(this.sucmsg);
      this.ionViewWillEnter();
    }
  }

  async presentAlert(id) {
    const alert = await this.alertCtrl.create({
      cssClass: 'my-alert-class',
      subHeader: 'Do you really want to delete it ?',
      buttons: [{
        text: 'Cancel'
      }, {
        text: 'yes',
        handler: () => {
          this.delete_department(id)
        }
      }]
    });

    await alert.present();
  }
  

  delete_department(id) {
    this.main_services.deleteDepartment(id).subscribe(
      (data) => {
        this.sucmsg = data['message'];
        this.toastr.success(this.sucmsg);
        this.ionViewWillEnter();
      }, (error) => {
        console.log(error);
      }
    )
  }

  
  department_search(searchData) {
    this.deptList = [];
    this.pageCounts=[];
    this.sortedData = [];
    this.main_services.searchDepartmentApi(searchData).subscribe(

      (data) => {


        data['departments'].forEach((value) => {
          this.deptList.push({
            _id: value._id,
            branch: value.branch.name,
            name: value.name,
            description: value.description
          });
      });
       
      this.sortedData = this.deptList.slice();
  }


    )


  }








}