import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { Router } from '@angular/router';
import { HeaderComponent } from './../../../common/header/header.component';
import $ from 'jquery';
import { MainServicesService } from './../../../services/main-services.service';
import { ModalController } from "@ionic/angular";
import { ActivatedRoute } from '@angular/router';
import { NavParams } from '@ionic/angular';
import { LoaderService } from 'src/app/services/loader.service';

@Component({
  selector: 'app-edit-dept-modal',
  templateUrl: './edit-dept-modal.component.html',
  styleUrls: ['./edit-dept-modal.component.scss'],
})
export class EditDeptModalComponent implements OnInit {
  editDepartmentForm:FormGroup;
  branches_list;
  deptId;
  deptData;
  errMsg;
  @Input() _id: any;
  getId = this.navParams.get('_id');
  valuedeptName = true;
  valuedeptDesc = true;
  valuebranch = true;
  line_value_name="none";
  showBtn;
  line_value_desc = "none";
  line_value_branch = "none";
  disabledbtn=true;
  descRight:boolean=false;
  nameRight:boolean=false;
  branchRight:boolean=false;

  constructor(private formbuilder: FormBuilder, private router: Router, private route: ActivatedRoute,
    private departmentData: MainServicesService, private modalController: ModalController,
    public navParams: NavParams, private loader:LoaderService) {
      this.editDepartmentForm = this.formbuilder.group({
        branch: ['',Validators.required],
        name:['',Validators.required],
        description:['',Validators.required]
      })
      // console.log(navParams.get('_id'));
     }

  ngOnInit() {}
  editDeptName(){
    this.showBtn = true;
    this.line_value_name = "line";
    this.valuedeptName = false;
    $('.token-type-select-box').removeClass('selection-border');
  }
  Insertname(){
    $('.deptname-pencil').hide(); 
    this.nameRight=true;
  }

  editBranch(){
    $('.token-type-select-box').addClass('selection-border');
     this.showBtn = true;
     this.disabledbtn=false;
    // $('.dept-pencil').hide(); 
   
  }
  selectBranch(){
   
    $('.dept-pencil').hide(); 

    this.branchRight=true;
  }

  editDeptDesc(){
    $('.token-type-select-box').removeClass('selection-border');
    this.showBtn = true;
    this.line_value_desc = "line";
    this.valuedeptDesc = false;
  }
  Insertdesc(){
    $('.desc-edit-pencil-icon').hide();
    this.descRight=true;
  }

  
  ionViewWillEnter() {
   

    this.departmentData.getBranchesList().subscribe((result)=>{
      this.branches_list=result["branches"];
      console.log(this.branches_list);
      
  })
  
  this.departmentData.editDepartment(this.getId).subscribe((result)=>{
    this.deptData=result['department'];  
    this.editDepartmentForm = this.formbuilder.group({
      branch: [this.deptData.branch._id],
      name: [this.deptData.name],
      description:[this.deptData.description]
    })
  })
  }
  
  updateDepartment(value)
{
  // console.log(value)
  this.departmentData.updateDepartment(this.getId,value).subscribe((result)=>{
    this.modalController.dismiss(result, 'success');    
  },
  (error)=>{
    console.log(error);
    this.errMsg = error['error']['error']['name'];
    if(this.errMsg){
      $('#errors').fadeIn(1000);
    }
    // this.modalController.dismiss(error, 'success');

  }
  )
}
dismiss() {
  this.modalController.dismiss({
    'dismissed': true
  });
}

}
