import { Component, OnInit } from '@angular/core';
import $ from 'jquery';
import { MainServicesService } from './../../../services/main-services.service';
import { Router } from '@angular/router';
import { ModalController } from "@ionic/angular";
import { LoaderService } from 'src/app/services/loader.service';

@Component({
  selector: 'app-add-department-modal',
  templateUrl: './add-department-modal.component.html',
  styleUrls: ['./add-department-modal.component.scss'],
})
export class AddDepartmentModalComponent implements OnInit {
  departments_list;
  sucmsg;
  errMsg;
  brancherr;
  deptName:string;
  deptDesc:string;
  branch:string;
  departmentData:any;
  
  constructor( private router: Router, private main_services: MainServicesService, private modalController: ModalController
    , private loader:LoaderService) { }

  ngOnInit() {}
  ionViewWillEnter() 
  {
  
  this.main_services.getBranchesList().subscribe((result)=>{
    this.departments_list=result["branches"];
})


}

dept_name(e){
  this.deptName = e.target.value
}

dept_description(e){
  this.deptDesc = e.target.value
}

branch_name(e){
 
  $('.select-item-list-popup-models label.branch_label').hide(); 
  this.branch=e.value;
  if(this.branch == ''){
  $('.select-item-list-popup-models label.branch_label').show(); 
  }
}
submit()
{
 let addDept = {
   name : this.deptName,
   branch:this.branch,
   description : this.deptDesc
 }

  this.main_services.addDepartment(addDept).subscribe((result)=>{
    this.modalController.dismiss(result, 'success');

  },
  (error)=>{
    console.log(error);
    this.errMsg = error['error']['error']['name'];
    this.brancherr = error['error']['error']['branch']
    if(this.errMsg){
      $('#errors').fadeIn(1000);
    }
    if(this.brancherr){
      $('#errors').fadeIn(1000);
    }
    

  })
 
  
}

dismiss() {
  this.modalController.dismiss({
    'dismissed': true
  });
}
}
